@import "variables";
@import "mixins";

html,
body {
    font-family: $fm;
}

.wrapper {
    max-width: 1110px;
    margin: 0 auto;
}
.flex {
    display: flex;
    &_wrap {
        flex-wrap: wrap;
    }
}
.center {
    text-align: center;
}
a {
    text-decoration: none;
    color: #5dc789;
}
.scroll_hidden {
    overflow: hidden!important;
}
.btn {
    display: inline-block;
    padding: 12px 30px;
    border: 1px solid $green;
    border-radius: 4px;
    background: none;
    text-decoration: none;
    transition: background ease 0.4s, border-color ease 0.4s, color  ease 0.4s;
    &:hover {
        text-decoration: none!important;
        background: $green;
        color: $white;
    }
    &.green {
        background: $green;
        border-color: $green;
        color: $white;
        &:hover {
            background: none!important;
            border-color: $green; 
            color: $white;
            text-decoration: none!important;
        }
        &_text:hover {
            color: $green;
        }
    }
    &.bold {
        font-family: $fb;
        font-size: 18px;
    }
    &.white {
        border-color: $white;
        &:hover {
            border-color: $green;
            background: $green;
            color: $white!important;
        }
    }
    &_big {
        padding-top: 25px;
        padding-bottom: 25px;
        font-family: $fb;
        font-size: 18px;
    }
    &_login {
        text-align: center;
        color: $white;
        font-family: $fb;
        &-fb {
            background: #3a549f;
            border-color: #3a549f;
            &:hover {
                border-color: #3a549f;
                color: #3a549f;
                background: none;
            }
        }
        &-gp {
            background: #de4b39;
            border-color: #de4b39;
            &:hover {
                border-color: #de4b39;
                color: #de4b39;
                background: none;
            }
        }
        
    }
    &_mob {
        @media (max-width:767px) {
            position: relative;
            cursor: pointer;
            float: right;
            margin-right: 15px;
            padding: 9px 10px;
            margin-top: 8px;
            margin-bottom: 8px;
            background-image: none;
            border: 1px solid #ddd;
            border-radius: 4px;
            &.open {
                span {
                    &:first-child {
                        transform: translate(0,6px) rotate(-45deg);
                        & + span {
                            opacity: 0;
                        }
                    }
                    &:last-child {
                        transform: translate(0,-6px) rotate(45deg);
                    }
                    
                }
            }
            span {
                display: block;
                width: 22px;
                height: 2px;
                border-radius: 1px;
                background: $green;
                margin-bottom: 4px;
                transition: ease all 0.4s;
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
}
.p {
    &_find-person {
        font-size: 18px;
        font-family: $fm;
        margin-bottom: 35px;
        & + p {
            font-size: 18px;
            font-family: $fm;
            margin-bottom: 20px;
        }
    }
}
input {
    width: 100%;
    background: $lgreen;
    border-radius: 5px;
    padding: 12px 9px;
    
    font-family: $fm;
    font-size: 16px;
    @include plhl() {
        color: $dgreen;
    }
    &[type="checkbox"],
    &[type="radio"] {
        display: none;
        & + .radio_btn,
        & + .checkbox {
            border: 3px solid;
            padding: 12px;
            text-align: center;
            border-radius: 4px;
            user-select: none;
            &_lit {
                width: 20px;
                min-width: 20px;
                height: 20px;
                border-radius: 4px;
                background: $lgreen;
                display: block;
                margin-right: 14px;
            }
        }
        & + .radio {
            width: 20px;
            height: 20px;
            position: relative;
            border-radius: 50%;
            background: $lgreen;
            display: inline-block;
            vertical-align: middle;
            
            margin-right: 10px; 
        }
        &:not(:checked) {
            & + .radio_btn,
            & + .checkbox {
                background: $lgreen;
                border-color: $lgreen;
            }
        }
        &:checked {
            & + .radio_btn {
                border-color: $green;
                background: $white;
            }
            & + .checkbox {
                border-color: $green;
                background: $white;
                &_lit {
                    position: relative;
                    color: $green;
                    font-size: 12px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    &:after {
                        content: "\e90c";
                        font-family: 'icomoon' !important;
                        speak: none;
                        font-style: normal;
                        font-weight: normal;
                        font-variant: normal;
                        text-transform: none;
                        line-height: 1;
                        -webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;
                    }
                }
            }
            & + .radio {
                &:after {
                    content: '';
                    display: block;
                    position: absolute;
                    width: 10px;
                    height: 10px;
                    background: $green;
                    border-radius: 50%;
                    left: 5px;
                    top: 5px;
                    
                    
                }   
            }
        }
    }
    &[type="checkbox"] {
        
    }
}
textarea {
    width: 100%;
    background: $lgreen;
    border-radius: 5px;
    padding: 12px 9px;
    min-height: 150px;
    max-height: 150px;
    font-family: $fm;
    font-size: 16px;
    @include plhl() {
        color: $dgreen;
    }
}
label {
    cursor: pointer;
}
.label {
    &_head {
        font-family: $fm;
        font-size: 18px;
        padding-bottom: 12px;
        span {
            &.grey {
                color: $grey;
            }
        }
    }
}
.block {
    &_head {
        font-family: $fb;
        font-size: 24px;
        font-weight: normal;
        
        padding-bottom: 15px;
        &_lit {
            font-size: 18px;
        }
        &_m {
            font-family: $fm;
        }
        li {
            font-size: 24px;
        }
        span {
            &.green {
                color: $green;
            }
        }
    }
    &_content {
        padding-bottom: 15px;
    }
    .section {
        &_head { 
            padding-bottom: 30px;
        }
    }
}
.section {
//    padding-top: 60px;
    padding-bottom: 60px;
    @media (max-width:992px) {
//        padding-top: 20px;
        padding-bottom: 20px;
    }
    &_head {
        font-size: 34px;
        font-family: $fb;
        
        padding-bottom: 60px;
        @media (max-width:992px) {
            padding-bottom: 35px;
            
        }
        .our_work-text {
            padding-top: 20px;
        }
        &.center {
            text-align: center;
        }
    }
    &.green {
        background: $lgreen; 
        padding-top: 60px;
        & + .section {
            margin-top: 60px;
        }
    }
}

ul {
    &.list {
        font-size: 18px;
        font-family: $fm;
        li {
            position: relative;
            .our_work-text {
                padding-top: 10px;
            }
            &:before {
                content: '\e90c';
                display: block;
                  font-family: 'icomoon' !important;
                  speak: none;
                  font-style: normal;
                  font-weight: normal;
                  font-variant: normal;
                  text-transform: none;
                  line-height: 1;
                
                color: $green;
                position: absolute;
                left: -33px;
                top: 3px;
                
            }
            & + li {
                margin-top: 15px;
            }   
        }
    }
}


