        }
    }
    &.profile_icons {
        color: #000;
        font-size: 18px;
        white-space: nowrap;
        @media (max-width: 500px) {
            
            font-size: 14px;
        }
        i {
            min-height: 47px;
            display: flex;
            justify-content: center;
            align-items: flex-end;
            color: $green;
            margin-bottom: 10px;
            .icon-dog-s {
