.report {
    color: #7c7c7c;
    font-size: 16px;
}

.experience {
    font-size: 17px;
    color: #000;
    i {
        color: $green;
        font-size: 17px;
    }
}



.search_map {
    position: relative;
    @media (max-width:1100px) {
        display: none;
    }
}

.map_in {
    width: 100%;
}

.dd_menu {
    display: none;
    top: 99%;
    position: absolute;
//    background: #fff;
//    border-radius: 0 10px 10px 0;
//    padding: 10px;
    position: absolute;
    right: 0;
    a {
        color: $green!important; 
        white-space: nowrap;
    }
    &-block {
        background: #fff;
        position: absolute;
        right: 100%;
        width: 200px;
        top: 0;
//        background: #fff;
        border-radius: 10px 0 0 10px;
        border: 1px solid $green;
        border-right: none;
        color: #000;
        padding: 10px;
        bottom: 0;
//        box-shadow: -2px 0px 11px #d2f1e0;
        &-head {
            text-align: center;
            font-family: $fb;
            margin-bottom: 10px;
        }
    }
    &_left {
        background: #fff;
        border: 1px solid $green;
//        box-shadow: 2px 0px 11px #d2f1e0;
        border-radius: 0 10px 10px 0;
        overflow: hidden;
        a {
            padding: 7px 20px 8px 20px!important;
            display: block;
            font-size: 16px;
            color: #000;
            width: 100%;
            &:hover {
                background: $lgreen;
                color: #000!important; 
            }
            &:first-child {
                border-radius: 0 10px 0 0;
            }
            &:last-child {
                border-radius: 0 0 10px 0;
            }
            
        }
        & > div {
        
            height: 1px;
            background: $green;
        }
    }
}

.pagination {
    
    a {
        background: #fff;
        border: 2px solid $green;
        padding: 15px 20px;
        border-radius: 4px;
        font-size: 16px;
        color: $green;
        display: inline-block;
        &.active,
        &:hover {
            background: $green;
            color: #fff;
        }
    }
    span {
        padding-left: 15px;
        padding-right: 15px;
        color: $green;
    }
    .search_center {
        border: none;
    }
    &_block {
        max-width: 450px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 45px;
        padding-bottom: 100px;
    }
}
.show_map.close {
    i {
        transform: rotate(-180deg);
    }
}
