header {
    font-size: 19px;
    
    background: url(../images/temp/1.png) no-repeat top center;
    background-size: cover;
    margin-bottom: 60px;
    position: relative;
    z-index: 2;
    .green_form {
        max-width: 600px;
        margin: 0 auto;
        & > * {
            padding-top: 15px!important;
            padding-bottom: 15px!important; 
        }
        input {
            padding-left: 25px;
        }
        button {
            padding-left: 30px!important;
            padding-right: 30px!important;
        }
    }
    .tags_search {
        padding-bottom: 60px;
        font-size: 17px;
    }
}
.header {
    &_top {
        padding-top: 44px;
        padding-bottom: 73px;
        position: relative;
        z-index: 2;
        @media (max-width:500px) {
            padding-bottom: 25px;
        }
        @media (max-width:767px) {
            padding-top: 10px;
            padding-bottom: 10px;
            
            background: #fff;
            & > div {
                &:first-child {
                    justify-content: space-between;
                    align-items: center;
                    display: flex;
                }
            }
        }
        
        &-right {
            align-items: center;
            justify-content: flex-end;
            a {
                display: inline-block;
                white-space: nowrap;
            }
        }
        &-center {
            align-items: center;
            justify-content:  center;
            a {
                display: inline-block;
            }
        }
        & > div:not(:first-child) {
//            padding-top: 19px;
        }
        .row {
            flex-wrap: nowrap;
        }
        a:not(.logo) {
            color: $white;
            text-decoration: none;
            padding: 7px 12px 8px 12px;
            transition: ease all 0.4s;
            i {
                vertical-align: text-top;
            }
            &:hover {
                color: $green;
            }
            @media (max-width:767px) {
                color: #000;
            }
        }
    }
    &_main {
        &-form {
            padding: 34px 21px;
            border-radius: 10px;
            background: $white;
            
            label {
                color: $green;
                font-size: 16px;
                
            }
            .label {
                margin-bottom: 15px;
            }
            .btn {
                font-size: 24px;
                width: 100%;
                box-shadow: 0 8px 27px $btnS;
                font-family: $fb;
                padding-top: 17px;
                padding-bottom: 17px;
                
                border-radius: 10px;
                i {
                    font-size: 34px;
                    vertical-align: middle;
                }
            }
            &-top {
//                margin-bottom: 15px;
//                @media (min-width:992px) and (max-width:1055px)  {
//                    .col {
//                        width: 100%;
//                        min-width: 100%;
//                        margin-bottom: 10px;
//                    }
//                }
            }
        }
        &-label {
//            margin-right: -5px;
//            margin-left: -5px;
//            .col {
//                padding-left: 5px!important;
//                padding-right: 5px!important;
//                
//            }
            margin-bottom: -15px;
//            @media (min-width:992px) and (max-width:1055px) {
//                .col-3 {
//                    -ms-flex: 0 0 50%;
//                    flex: 0 0 50%;
//                    max-width: 50%;
//                    margin-bottom: 15px;
//                }
//            } 
            label {
                white-space: nowrap;
                margin-bottom: 15px;
            }
            b {
                display: block;
            }
            input[type="checkbox"] + .checkbox {
                padding-left: 0px;
                padding-right: 0px;
            }
        }
        label {
            padding-top: 17px;
            padding-bottom: 17px;
        }
    }
    &_h1 {
        font-size: 42px;
        color: $white;
        font-family: $fl; 
        margin-bottom: 25px; 
        font-weight: normal;
        max-width: 730px;
        margin-left: auto;
        margin-right: auto;
        span {
            border-bottom: 1px solid $green;
        }
        @media (max-width:600px) {
            font-size: 28px;
        }
    }
    &_h2 {
        font-size: 26px;
        color: $white;
        font-family: $fl; 
        font-weight: normal;
        max-width: 540px;
        margin: 0 auto;
        margin-bottom: 25px; 
        @media (max-width:600px) {
            font-size: 18px;
        }
    }
    &_user {
        align-items: center;
        color: $white;
        justify-content: flex-end;
        &-img {
            width: 42px;
            height: 42px;
            min-width: 42px;
            min-height: 42px;
            border-radius: 50%;
            overflow: hidden;
            padding: 0!important;
            
            margin-right: 10px;
        }
        &-name {
            margin: 0 12px;
            position: relative;
            cursor: pointer;
            &:hover {
//                background: #fff;
                .dd_menu {
                    display: block;
                }
            }
            & >div {
                &:first-child {
                    font-family: $fm;
                    font-size: 19px;
                    
                    i {
                        font-size: 9px;
                        transform: rotate(-90deg);
                        display: inline-block;
                        vertical-align: middle;
                        margin-left: 4px;
                        margin-right: 4px;
                        cursor: pointer;
                    }
                } 
            }
        }
        &-notification {
            background: $green;
            padding:9px 16px 7px 16px;
            border-radius: 4px;
            font-family: $fm;
            font-size: 19px;
            i {
                display: inline-block;
                vertical-align: top;
                margin-top: 1px;
            }
            &:hover {
                background: none;
                box-shadow: 0 0 0 2px $green inset;
                color: $white!important; 
            }
        }
        
        @media (max-width: 767px) {
            padding-top: 10px;
            justify-content: flex-start;
            align-items: flex-start;
            .header_user-img {
                max-width: 40px!important;
                min-width: 40px!important;
                width: 40px!important;
            }
            .header_user-name,
            a {
                color: #000;
                width: 100%;
            }
            .dd_menu-block,
            .header_user-name .icon-back,
            a.header_user-notification {
                display: none!important;
                opacity: 0;
                height: 0;
                visibility: hidden;
            }
            .dd_menu {
                position: relative;
                display: block!important;
            }
            .dd_menu_left {
                border:none;
            }
            .dd_menu_left a {
                padding: 7px 20px 8px 0px !important;
            }
            .dd_menu_left > div {
                display: none;
            }
        }
    }
    &_dd {
        @media (max-width:767px) {
            display: none;
            margin-top: 10px;
            a {
                &:not(.btn) {
                    display: block!important;
                }
                
            }
            .header_top-right {
                border-top: 1px solid $green;
            }
        }
    }
    &_mob {
        padding-bottom: 50px;
        padding-top: 25px;
        background-size: cover;
        background-position: top center;
        @media (min-width:767px) {
            background: none!important;
            
        }
    }
}
.banner {
    &_main {
        padding-bottom: 125px;
        @media (max-width:500px) {
            padding-bottom: 30px;
        }
        &-left {
            color: #fff;
            
        }
        h1 {
            font-family: $fl;
            font-weight: normal;
            font-size: 56px;
            line-height: 49px;
            padding-bottom: 28px;
            @media (max-width:500px) {
                font-size: 36px;
                line-height: 32px;
                padding-bottom: 10px;
            }
            br {
                display: none;
            }
            span {
                border-bottom: 1px solid $green;
            }
        }
        h2 {
            font-family: $fl;
            font-weight: normal;
            font-size: 31px;
            @media (max-width:992px) {
                margin-bottom: 15px;
            }
            @media (max-width:500px) {
                font-size: 24px;
            }
        }
    }
    
}
