
.info {
    @media (max-width:992px) {
        margin-bottom: 50px;
        & > div {
            &:first-child {
                padding-top: 25px;
                order: 5;
            }
        }
    }
    @media (max-width:600px) {
         margin-bottom: 0px;
    }
    &_green {
        background: $lgreen;
        color: #3f7254;
        border-radius: 10px;
        font-size: 18px;
        text-align: center;
        padding-bottom: 37px;
        &-head {
            padding: 28px 10px 26px;
            
            color: $black;
            background: $hgreen;
            border-radius: 10px 10px 0 0;
            font-size: 19px;
            
            font-family: $fm;
        }
        li {
            text-align: left;
            line-height: 23px;
//            & + li {
//                padding-top: 15px;
//                
//            }
        }
        .btn {
            margin-top: 11px;
            padding-left: 40px;
            padding-right: 40px;
        }
        &-block {
            padding-left: 15px;
        }
    }
    &_bottom {
        color: $grey;
        padding: 25px 0;
        text-align: center;
        line-height: 23px;
        a {
            color: $textgreen;
            text-decoration: none;
            &:hover {
                text-decoration: underline;
            }
        }
    }
}

.serv {
    &_item {
        display: flex;
        color: $grey;
        font-size: 18px;
        line-height: 23px;
        margin-bottom: 15px;
        & + & {
            margin-top: 35px;
            @media (max-width:768px) {
                margin-top: 0;
            }
        }
        i {
            text-align: center;
            font-size: 65px;
            min-width: 65px;
            margin-right: 30px;
            color: $green;
            &.icon-safe-payment {
                font-size: 50px;
                
            }
            @media (max-width:500px) {
                font-size: 40px;
                min-width: 40px;
                margin-right: 10px; 
                &.icon-safe-payment {
                    font-size: 30px;

                }
            }
        }
    }
    .block_head {
        color: $black;
    }
}

.rev {
    padding-top: 50px;
    & + & {
        border-top: 1px solid $lgreen;
    }
    &_photo {
        position: relative; 
        img {
            display: block;
            width: 100%;
            height: auto;
        }
        &-img {
            overflow: hidden;
            border-radius: 10px;
            z-index: 2;
            position: relative;
        }
        &-text {
            position: absolute;
            right: 95px;
            top: -55px;
            padding: 37px 47px;
            border-radius: 10px;
            background: $lgreen;  
            max-width: 350px; 
            font-size: 22px;
            line-height: 28px;
            z-index: 3;
            font-family: $fm;
            @media (max-width:600px) {
                padding: 35px 20px 20px; 
                margin-top: -15px;
                z-index: 1;
            }
            div {
                padding-top: 15px;
                color: $green;
            }
            @media (max-width:600px) {
                position: relative;
                right: auto;
                top: auto;
                max-width: 100%;
            }
        }
        &.col {
            margin-bottom: 120px;
            @media (max-width:600px) {
                margin-bottom: 15px;
            }
            .rev {
                &_photo {
                    &-text {
                        top: auto;
                        bottom: -105px;
                        @media (max-width:600px) {
                            bottom: auto;
                        }
                    }
                }
            }
        }
        &-info {
            position: absolute;
            left: 17px;
            bottom: 24px;
            background: rgba(0,0,0,0.2);
            color: $white;
            padding: 14px 16px;
            border-radius: 10px;
            font-size: 18px; 
            z-index: 2;
            font-family: $fb;
            @media (max-width:600px) {
                left: 17px;
                bottom: auto;
                top: 17px;
            }
        }
    }
    &_block {
        font-family: $fm;
        font-size: 15px;
        line-height: 18px;
        
        
        &-head {
            align-items: center;
            .sitter_info-name {
                margin-right: 15px;
            }
            .star {
                margin-top: 5px;
            }
        }
        &-img {
            overflow: hidden;
            border-radius: 50%;
            margin-right: 20px;
            img {
                display: block;
            }
        }
        &-item {
            padding-top: 30px;
            padding-bottom: 25px;
            & + & {
                border-top: 1px solid $lgreen;
            }
        }
        &-check {
//            font-size: 15px;
            color: #7c7c7c;
            i {
                color: $green;
            }
        }
        &-text { 
            padding-top: 25px; 
        }
    }
}

.our_work {
    text-align: center;
//    padding-bottom: 80px;
    &-item {
        padding-bottom: 20px;
        @media (min-width:768px) {

            z-index: 2;
            &:last-child {
                z-index: 1;
                .our_work-number {

                    &:before {
                        content: '';
                        position: absolute;
                        right: 99%;
                        left: -99999999%;
                        height: 1px;
                        top: 50%;
                        margin-top: -0.5px;
                        background: $lgreen;
                    }
                }
            }
            &:first-child {
                .our_work-number {
                    &:before {
                        content: '';
                        position: absolute;
                        right: 99%;
                        left: -99999999%;
                        height: 1px;
                        top: 50%;
                        margin-top: -0.5px;
                        background: $white;
                    }
                }
            }
        }
    }
    &-number {
        width: 60px;
        height: 60px;
        background: $lgreen;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        color: $textlgreen;
        position: relative;
        margin: 0 auto;
    }
    &-icon {
        color: $textlgreen;
        font-size: 60px;
        padding-top: 30px;
        padding-bottom: 20px;
        max-height: 105px;
        @media (max-width:500px) {
            padding-top: 8px;
            padding-bottom: 0px;
        }
        .icon-search {
            font-size: 70px;
        }
        .icon-card {
            font-size: 70px;
        }
        
    }
    &-text {
        font-size: 18px;
        color: $grey;
        line-height: 23px;
        @media (max-width:500px) {
            padding-bottom: 25px;   
        }
        &-black {
            color: #000;
        }
    }
}

.tags {
    &_search {
        font-size: 15px;
        color: $green;
        span {
            color: #75ad8c;  
        }
        a {
            color: $green!important;
            padding-right: 0!important;
            &:hover {
                text-decoration: underline;
            }
        }
    }
}
.bread {
    padding: 15px 0;
    border-bottom: 1px solid $lgreen;
    color: $textgreen;
    
    margin-top: -60px;
    margin-bottom: 60px;
    a {
        color: $textgreen;
        text-decoration: none;
        display: inline-block;
        &:hover {
            text-decoration: underline;
        }
        
    }
    span {
        display: inline-block;
    }
    &_right {
        font-size: 18px;
        a {
            text-decoration: none;
            i {
                font-size: 15px;
            }
            &:hover {
                text-decoration: none;
                span {
                    text-decoration: underline;
                }
            }
        }
    }
}

.find-person {
    text-align: left;
    color: $bgreen;
    font-family: $fm;
    font-size: 18px;
    b {
        display: block;
        font-weight: normal;
        font-family: $fb;
        margin-bottom: 23px;
    }
}

.form {
    &_login {
        box-shadow: 0 1px 25px #d2f1e0;
        padding: 55px 65px;
        border-radius: 15px;
        font-family: $fm;
        @media (max-width:600px) {
            padding: 25px;
        }
        .btn {
            min-width: 100%;
            font-family: $fb;
            font-size: 18px; 
        }
//        &-social {
//            margin-bottom: 
//        }
        .label {
            margin-bottom: 15px;
        }
        a:not(.btn) {
            &:hover {
                text-decoration: underline;
            }
        }
    }
    &_policy {
        color: $grey;
        font-size: 14px;
        line-height: 22px;
        font-family: $fm; 
    }
    &_add {
        &-email {
            input {
                margin-bottom: 20px;
            }
            .btn {
                padding: 6px 12px 8px 12px;
                
            }
        }
        &-dog {
            .btn {
                padding: 6px 12px 8px 12px;
            }
            &_main {
                position: relative;
                .remove_dog {
                    position: absolute;
                    top: -12px;
                    right: 0px;
                    width: 30px;
                    height: 30px;
                    box-shadow: 0 1px 25px #d2f1e0;
                    border-radius: 50%;
                    color: #fff;
                    text-align: center;
                    &:after,
                    &:before {
                        content: '';
                        position: absolute;
                        display: block;
                        width: 15px;
                        height: 1px;
                        top: 50%;
                        left: 50%;
                        background: $green;
                        transform: translate(-50%,-50%) rotate(-45deg);
                    }
                    &:after {
                        transform: translate(-50%,-50%) rotate(45deg);
                    }
                }
            }
        }
    }
}

.entrance {
    margin-top: -60px;
    position: relative;
    font-size: 19px;
    font-family: $fm;
    & > * {
        position: relative;
        z-index: 2;
    }
    &:before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 50%;
        z-index: 1;
        background: $lgreen;
        @media (max-width:576px) {
            right: 0%;
            bottom: 50%;
        }
    }
    &-block {
        padding-top: 60px;
        padding-bottom: 130px;
        @media (max-width:576px) {
            padding-top: 20px;
            padding-bottom: 30px;
            text-align: center;
        }   
    }
    i {
        font-size: 72px;
        color: $green;
        display: block;
        margin-bottom: 18px; 
    }
    .btn {
        font-family: $fb;
    }
    .section_head {
        padding-bottom: 20px;
    }
}

.help {
    &_block {
        color: #757575;
    }
    p {
        margin-bottom: 10px;
        & + p {
            padding-top: 15px;
        }
    }
    ul {
        padding-left: 0;
        margin-left: 0;
    }
    .block_head {
        padding-bottom: 30px;
    }
    &_q {
        font-size: 18px;
        .block_head {
            color: $green;
            &:not(.block_head_g) {
                
                font-size: 18px;
            }
        }
        
        a:not(.btn) {
            color: $black;
            display: block;
            margin-bottom: 15px;
            font-size: 17px;
            font-family: $fm;
            &:hover {
                color: $green;
            }
        }
        .block_content {
            + .block_head {
                padding-top: 30px;
                
            }
        }
        .btn {
            margin-bottom: 60px;
        }
    }
    &_btn {
        &.flex {
            flex-wrap: wrap;
        }
        a {
            margin: 0 10px 10px 10px;
            min-width: 260px;
            @media (max-width:400px) {
                width: 100%;
            }
        }
    }
    &_top {
        display: flex;
        align-items: stretch;
        border: 1px solid $green;
        border-radius: 4px;
        overflow: hidden;
        margin-bottom: 30px;
        a {
            font-size: 18px;
            width: 100%;
            padding: 15px 0;
            background: $lgreen;
            display: flex;
            justify-content: center;
            align-items: center;
            & + a {
                border-left: 1px solid $green;
            }
            &:hover,
            &.active {
                background: none;
            }
            &.active {
                box-shadow: 0 0 0 2px $green inset;
            }
        }
    }
}

.search {
    &_main {
        background: #eaedee;
        display: flex;
        justify-content: space-between;
        align-items: stretch;
        margin-top: -60px;
        padding-left: 5px;
        padding-top: 5px;
        & + & {
            margin-top: 0;
        }
        & > * {
            width: 100%;
        }
        @media (max-width:768px) {
            display: block;
            padding-right: 5px;
        }
    }
    &_left {
        min-width: 260px;
        max-width: 260px;
        width: 260px;
        @media (max-width:768px) {
            min-width: 100%;
            max-width: 100%;
            width: 100%;
        }
    }
    &_center {
        margin-left: 5px;
        min-width: 635px;
        border-bottom: 1px solid #dcdcdc;
        @media (max-width:1300px) {
            min-width: 450px;
            
        }
        @media (max-width:768px) {
            min-width: 100%;
            margin-left: 0;
            .sitter_list {
                border-radius: 10px;
                margin-top: 5px;
            }
            
        }
        
        &.close {
            @media (min-width:1100px) {
                min-width: 400px;
                width: 400px;
                
                
           
                .sitter_main.sitter_main_b {
                    display: block;
                }
            }
        }
    }
    &_map {
        background: #fff;
        padding-left: 15px;
        padding-top: 10px;
        border-bottom: 1px solid #dcdcdc;
        &-top {
            margin-bottom: 10px;
            padding-right: 10px;
            font-size: 16px;
            i {
                font-size: 7px;
                color: $green;
                margin-right: 7px;
            }
            .align-items-center {
                color: $green;
                cursor: pointer;
            }
        }
    }
}
.filter {
    &_block {
        padding: 14px 10px;
        &:first-child {
            background: #fff;
            border-radius: 10px;
        }
        &-main { 
            label {
                color: #686868;
            }
            &-head {
//                background: #000;
                padding: 24px 0 20px 0;
            }
        }
        label {
            display: flex;
            margin-bottom: 15px;
        }
        input {
            background: none;
            border: 1px solid #dcdcdc;
            margin-bottom: 10px;
        }
        &-second {
            @media (max-width:768px) {
                overflow: hidden;
                overflow-y: scroll;
                position: fixed;
                z-index: 9999;
                top: 0;
                left: -110%;
                right: 110%;
                bottom: 0;
                background: #fff;
                padding: 10px;
                transition: all ease 0.4s;
                &.open {
                    left: 0%;
                    right: 0%;
                }
            }
            &-close {
                display: none;
                @media (max-width:768px) {
                    display: block;
                    text-align: right;
                    margin-bottom: 7px;
                }
            }
        }
        &-m_btn {
            display: none;
            @media (max-width: 768px) {
                display: block;
            }
            i {
                margin-right: 7px;
            }
        }
    }
    &_item {
        .icon-arrow-map {
            transform: rotate(180deg);
            margin: 0 7px;
            font-size: 7px;
            color: #c2c2c2;
                margin-bottom: 10px;
        }
        .jq-selectbox {
            margin-bottom: 15px;
        }
        &-name {
            margin-bottom: 10px;
        }
        span.checkbox {
            background: none!important;
            border: none;
            width: 100%;
            border-radius: 0;
            font-size: 14px;
            padding: 10px 2px!important;
        }
        *:checked + span.checkbox {
            box-shadow: 0 0 0 2px inset $green;   
        }
        label {
            width: 100%;
            & + label {
                border-left: 1px solid #dcdcdc!important;
            }
        }
        &-select .flex {
            border-radius: 4px;
            border: 1px solid #dcdcdc!important;
            margin-bottom: 15px;
            label {
                margin-bottom: 0;
            }
        }
    }
}

.calendar {
    &-block {
        padding: 30px;
        box-shadow: 0 1px 25px #d2f1e0;
        border-radius: 10px;
        overflow: hidden;
        margin-bottom: 30px;
        label {
            display: flex;
            align-items: center;
            font-family: $fm;
            margin-bottom: 10px;
        }
        b {
//            font-weight: normal;
            font-family: $fm;
            
        }
    }
    &-main {
        box-shadow: 0 1px 25px #d2f1e0;
        border-radius: 10px;
        overflow: hidden;
        margin-bottom: 30px;
        width: calc( 100% + 15px );
        margin-left: -15px;
        @media (max-width:600px) {
            width: 100%;
            margin-left: 0px;
        }
    }
    &_top {
        padding: 25px 55px 50px 55px; 
        @media (max-width:600px) {
            flex-direction: column;   
            padding-left: 15px;
            padding-right: 15px;
        }
        &-date {
            padding-bottom: 0!important;
            @media (max-width:600px) {
                padding-bottom: 10px!important;
            }
        }
        &-nav {
            .left {
                transform: rotate(-90deg)
            }
            .right {
                transform: rotate(90deg)
            }
            &-arr {
                margin-left: 25px;
            }
        }
    }
    &_row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        & + & {
            border-top: 1px solid #eaf7ef;
        }
        > div:not(.calendar_row) {
            padding: 10px;
            text-align: center;
            width: 100%;
            height: 76.35px;
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            user-select: none;
            cursor: pointer;
            &.green {
                background-color: $lgreen;
            }
            &.grey {
                background-color: #c3c3c3;
            }
            & + div {
                border-left: 1px solid #eaf7ef;
            }
        }
    }
    &_head {
        border-top: 1px solid $green;
        border-bottom: 1px solid $green;  
        > div {
            > div:not(.calendar_row) {
                height: auto!important;
            }
        }
        .green {
            color: $green;
            background: none!important;
        }
    }
    &_date {
        position: absolute;
        left: 10px;
        top: 10px;
        &.grey {
            color: #bababa;
        }
    }
    &_day {
        font-size: 14px;
        display: flex;
        justify-content: center;
        align-items: center;
        @media (max-width:580px) {
            span {
                display: none;
            }
        }
        
        i { 
            width: 13px;
            height: 13px;
            min-width: 13px;
            min-height: 13px;
            border-radius: 50%;
            background: $green;
            display: inline-block;
            margin-right: 6px;
            &.red {
                background: red;
            }
        }
        @media (max-width:450px) {
            flex-direction: column;
            
            i {
                margin: 10px 0 4px 0;
            }
        }
    }
    &_inline {
        width: calc( 100% + 30px );
        margin-left: -15px;
        &-head {
            background: #3b443f;
            color: $white;
            padding: 15px 0;
            display: flex;
            justify-content: center;
            align-items: center;
            div {
                &:not(.arr) {
                    margin: 0 10px;
                }
            }
            .arr {
                font-size: 7px;
                width: 26px;
                height: 26px;
                min-width: 26px;
                display: flex;
                justify-content: center;
                align-items: center;
                cursor: pointer;
                text-decoration: none;
                border-radius: 4px;
                &:hover {
                    box-shadow: 0 0 0 2px #79ffc4;
                }
                &-left {
                    transform: rotate(90deg);
                    
                }
                &-right {
                    transform: rotate(-90deg);
                    
                }
                
            }
        }
        &_row {
            display: flex;
            justify-content: center;
            align-items: center;
            user-select: none;
            & > div {
                min-width: 34px;
                min-height: 28px;
                display: flex;
                justify-content: center;
                align-items: center;
                text-align: center;
                margin: 3px 5px;
                border-radius: 4px;
                cursor: pointer;
                &.grey {
                    color: #c3c3c3;
                }
                &.day {
                    &_green {
                        background-color: $green;
                        color: $white;
                    }
                    &_grey {
                        background-color: #c3c3c3;
                        color: #fff!important;
                    }
                    
                }
                &.select {
                    
                }
            }
        }
        &-info {
            font-size: 16px;
            
            i {
                min-width: 28px;
                min-height: 28px;
                display: inline-block;
                margin: 3px 5px;
                border-radius: 4px;
                cursor: pointer;
                vertical-align: middle;
                &.green {
                    background: $green;
                }
                &.grey {
                    background: #c3c3c3;
                }
                
            }
        }
    }
}

.photo {
    &_drop {
        border: 2px dashed $green;
        border-radius: 10px;
        background: $lgreen;
        min-height: 145px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: $green;
        font-size: 18px;
        margin-bottom: 20px;
        cursor: pointer;
        i {
            font-size: 43px;
            margin-bottom: 15px;
        }
    }
    &_list {
        display: flex;
        flex-wrap: wrap;
        width: calc( 100% + 10px );
        margin-left: -5px; 
        margin-bottom: 20px;
        div {
            border: 2px solid $lgreen;
            width:calc( 25% - 10px );
            margin: 5px;
            min-height: 115px;
            border-radius: 10px;
            overflow: hidden;
            img {
                width: 100%;
                height: auto;
                
            }
        }
    }
}

.client_photo {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    padding-top: 20px;
    margin-bottom: 70px;
    img {
        width: 100%;
        height: auto;
    }
    a {
        width: 25%;
        overflow: hidden;
        display: block;
        border-radius: 10px;
        & + a {
            margin-left: 20px;
        }
    }
    &_a {
        position: relative;
        span {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            background: rgba(0,0,0,0.5);
            font-size: 30px;
            color: #fff;
        }
    }
}

.about {
    padding-top: 25px;
    &-head {
        color: $green;
        font-size: 23px;
        font-family: $fm;
        padding-bottom: 10px;
        &-black {
            color: #000;
            i {
                color: $green;
                vertical-align: top;
                display: inline-block;
                margin-right: 10px;
            }
        }
    }
    p {
        color: #7c7c7c;
        margin-bottom: 20px;
    }
    .list {
        color: #7c7c7c;
        font-size: 16px;
    }
}

.sitter {
    &_list {
        background: #fff;
        
        border-radius: 10px 0 0 0;
//        border-bottom: 1px solid #dcdcdc;
    }
    &_item {
        padding: 15px;
        & + & {
            border-top: 1px solid #eaedee;
        }
    }
    &_top {
        justify-content: space-between;
        align-items: center;
        padding: 10px 17px;
        border-radius: 7px;
        background: $lgreen;
        @media (max-width:550px) {
            white-space: nowrap;
            flex-direction: column;
        }
        i {
            border: 2px solid $green;
            color: $green;
            font-size: 12px;
            border-radius: 50%;
            padding: 4px;
            margin-right: 10px;
        }
    }
    &_info {
        & > .flex {
            justify-content: space-between;
            align-items: center;
            &:first-child {
                @media (max-width:600px) {
                    display: block!important;
                }
            }
        }
        .sitter_info-cost-block b {
            color: #d8682b;
        }
        &-adress {
            margin-right: 10px;
        }
        &-review {
            align-items: center;
            justify-content: space-between;
            font-size: 16px;
            padding-top: 10px;
            width: 100%;
            &-img {
                min-width: 40px;
                height: 40px;
                width: 40px;
                border-radius: 50%;
                overflow: hidden;
                margin-right: 8px;
                img {
                    display: block;
                }
            }
        }
        &-left {
            
        }
    }
    &_img {
        &-more {
            padding-top: 10px;
            display: flex;
            align-items: center;
            i {
                margin-right: 10px;
            }
        }
    }
}
