.profile { 
    box-shadow: 0 1px 25px #d2f1e0;
    border-radius: 10px;
    overflow: hidden;
    &_top {
        display: flex;
        justify-content: space-between;
        align-items: stretch;
        border-bottom: 2px solid $lgreen;
        a {
            padding: 15px 5px 17px ;
            width: 100%;
            text-align: center;
            position: relative;
            z-index: 12;
            background: $green;
            color: #80e5ad;
            cursor: default;
            
            
            display: flex;
            align-items: center;
            justify-content: center;
            
            @media (max-width:500px) {
                font-size: 12px;
            }
            
            & + a {
                border-left: 1px solid #67c992;
            }
//                &:after,
//                &:before {
//                    content: '';
//                    display: block;
//                    position: absolute;
//                    top: -5px;
//                    left: calc( 100% - 10px );
//                    bottom: -5px;
//                    width: 10px;
//                    -webkit-clip-path: polygon(0 0, 99% 50%, 0 100%, 0 94%, 90% 50%, 0 7%);
//                    clip-path: polygon(0 0, 99% 50%, 0 100%, 0 94%, 90% 50%, 0 7%);
//                    background: $white;
//                    opacity: 0.2;
//                }
            &.active {
                background: $green;
                color: $white;
                z-index: 10;
                
//                &:after,
//                &:before {
//                    -webkit-clip-path: polygon(0 0, 0% 100%, 100% 50%);
//                    clip-path: polygon(0 0, 0% 100%, 100% 50%);
//                    background: $green;
//                    opacity: 1;
//                    left: 100%;
//                    top: 0;
//                    bottom: 0;
//                }
                & ~ a {
                    z-index: 11;
                    background: none;
                    color: $green;
//                    cursor: pointer;
                }
            }
        }
    }
    &_content {
        padding: 20px;
        padding-bottom: 60px;
        @media (min-width:900px) {
            padding-left: 90px;
            padding-right: 90px;
        }
        &_b {
            padding-bottom: 40px!important;
        }
        & + & {
            border-top: 2px solid $lgreen;
            padding-top: 50px;
        }
        .our_work-text {
            margin-bottom: 25px;
        }
        .label {
            margin-bottom: 15px;
        }
        &_line {
            margin: 25px 0;
            height: 1px;
            width: calc( 100% + 50px );
            margin-left: -20px;
            margin-right: -20px;
            min-width: calc( 100% + 50px );
            max-width: calc( 100% + 50px );
            background: $lgreen;
        }
    }
    &_form {
        &-photo {
            width: 150px;
            height: 150px;
            background: $lgreen;
            border-radius: 10px;
            margin: 0 0 15px 0;
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            &_block {
                margin-bottom: 50px;
            }
            &-img {
                position: absolute;
                width: 100%;
                height: 100%;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                z-index: 2;
            }
            &:before {
                content: "\e939";
                font-size: 70px;
                color: #61a07c;
                  font-family: 'icomoon' !important;
                  speak: none;
                  font-style: normal;
                  font-weight: normal;
                  font-variant: normal;
                  text-transform: none;
                  line-height: 1;
                    
                  /* Better Font Rendering =========== */
                  -webkit-font-smoothing: antialiased;
                  -moz-osx-font-smoothing: grayscale;
            }
        }
        &-email {
            display: flex;
            align-items: center;
            margin-bottom: 25px;
            @media (max-width:500px) {
                display: block;
            }
            input {
                margin: 0 10px 0 25px; 
                width: auto;
                @media (max-width:500px) {
                    display: block;
                    text-align: left;
                    margin-left: 0;
                    padding-left: 0;
                }
            }
            [readonly] {
                background: none;
                color: #000;
            }
            .block_head {
                margin: 0!important;
                padding: 0!important;
            }
            &_new {
                color: $grey;
                display: none;
                input {
                    margin-right: 15px;
                }
                button {
                    white-space: nowrap;
                }
                p {
                    font-size: 18px;
                    font-family: $fm;
                    padding-bottom: 15px;
                    color: $black;
                }
                .center {
                    margin-top: 10px;
                }
                @media (max-width:500px) {
                    .flex {
                        flex-direction: column;
                        
                    }   
                    input {
                        margin-bottom: 10px;
                    }
                }
            }
        }
        &-sex {
            color: $grey;
            padding-top: 11px;
            display: flex;
            label {
                margin-right: 25px;
                white-space: nowrap;
                display: flex;
            }
        }
    }
    
    &_wrapp {
        overflow: hidden;
        width: 100%;
//        display: flex;
//        flex-wrap: nowrap;
        padding-bottom: 60px;
    }
    &_block {
        width: 100%;
        display: flex;
        flex-wrap: nowrap;
        &_in {
            width: 100%;
        }
        & + .label {
            .btn {
                margin-bottom: 7px;
            }
        }
    }
    &_q {
        justify-content: space-between;
        align-items: stretch;
        @media (max-width:650px) {
            flex-wrap: wrap;
            justify-content: center;
        }
        &-text {
            width: 100%;
            padding-right: 20px;
            padding-top: 15px;
//            color: $black;
//            font-size: 18px;
//            font-family: $fm;
        }
        &-btn {
            min-width: 190px;
            max-width: 190px;
            display: flex;
            flex-wrap: wrap;
            text-align: center;
            
            a {
                width: 100%;
                border: 3px solid $lgreen;
                text-align: center;
                background: $lgreen!important; 
                color: $dgreen;
                border-radius: 4px;
                    padding: 12px;
                
                transition: all ease 0.4s;
                &:hover {
                    color: $green; 
                    border-color: $green;
                }
            }
            label {
                width: calc( 50% - 5px );
                
                margin-bottom: 10px;
                & + label {
                    margin-left: 10px;
                }
            }
        }
        &-more {
            padding-top: 15px;
            display: none;
        }
    }
    &_menu {
        padding-bottom: 0;
        margin-bottom: 60px;
        margin-top: -60px;
        background: #102117;
        .flex {
            justify-content: center;
            flex-wrap: wrap;
            @media (min-width:715px) {
                display: flex!important;
            }
            @media (max-width:715px) {
                display: none;
            }
        }
        a {
            width: 20%;
            font-size: 19px;
            color: $white;
            padding: 17px 0;
            &:hover,
            &.active {
                color: $green;
            }
            @media (max-width:1135px) {
                width: 33%;
            }
            @media (max-width:715px) {
                width: 100%;
                display: block; 
            }
        }
        i {
            color: $green;
            vertical-align: text-bottom;
            padding-right: 4px;
        }
        &-btn {
            display: flex;
            justify-content: flex-end;
            width: 100%;
            @media (min-width:715px) {
                display: none;
            }
            div {
                display: block;
                width: 40px;
                height: 40px;
                cursor: pointer;
                padding-top: 10px;
                &.open {
                    span {
                        &:first-child {
                            margin: 7px 0 -7px 0;
                            transform: rotate(45deg);
                            & + span {
                                opacity: 0;
                            }
                        }
                        &:last-child {
                            margin: -5px 0 5px 0;
                            transform: rotate(-45deg);
                        }
                    }
                }
            }
            
            span {
                width: 30px;
                height: 4px;
                background: $green;
                border-radius: 5px;
                display: block;
                margin-bottom: 4px;
                transition: all ease 0.4s;
            }
        }
    }
    &_icons {
        min-height: 40px;
        display: flex;
        justify-content: center;
        align-items: flex-end;
        font-size: 30px;
        color: $green;
        .icon-dog-m {
            font-size: 30px;
            transform: rotateY(-180deg)
        }
        .icon-dog-l {
            font-size: 41px;
            transform: rotateY(-180deg)
        }
        .icon-dog-xl {
            font-size: 47px;
            transform: rotateY(-180deg)
        }
        .icon-c-no {
            font-size: 24px;
        }
        .icon-c-dog2 {
            font-size: 40px;
            transform: rotateY(-180deg)
        }
        
        &-dogs {
            [type="checkbox"]:not(:checked) + .checkbox,
            [type="checkbox"]:checked + .checkbox {
                color: #000!important;
                font-family: $fm;
                font-size: 18px;
                b {
                    font-family: $fm;
                    font-weight: normal;
                }
            }
            .profile_icons {
                min-height: 70px;
            }
        }
        
        
    }
    [type="checkbox"],
    [type="radio"] {
        & + .radio_btn,
        & + .checkbox {
            font-family: $fm;  
            padding: 12px 2px;
            b {
                display: inline-block;
            }
            &.font_size {
                &-lit {
                    font-size: 9px;
                }
            }
        }
        &:not(:checked) {
            & + .radio_btn,
            & + .checkbox {
                background: $lgreen;
                border-color: $lgreen;
                color: $dgreen;
            }
        }
        
    }
    
    
    &_info {
        margin-top: -60px;
        background: #eefcf5;
        padding-top: 50px;
        padding-bottom: 50px;
        &-slider {
            position: relative;
            img {
                display: block;
                width: 100%;
            }
            &-nav {
                position: absolute;
                z-index: 10;
                display: flex;
                bottom: 15px;
                left: 15px;
                div {
                    margin-right: 10px;
                    overflow: hidden;
                    border-radius: 5px;
                    cursor: pointer;
                    position: relative;
                    &:hover {
                        img {
                            position: relative;
                            z-index: 1;
                        }
                        &:before {
                            content: '';
                            position: absolute;
                            z-index: 2;
                            top: 0;
                            left: 0;
                            bottom: 0;
                            right: 0;
                            height: 100%;
                            width: 100%;
                            box-shadow: 0 0 0 3px $green inset;
                        }
                    }
                }
            }
            &-big {
                position: relative;
                z-index: 5;
                overflow: hidden;
                border-radius: 10px;
            }
        }
        &-white {
            background: #fff;
            border-radius: 10px;
            .sitter_main {
                display: block;
                
                @media (max-width:450px) {
                    & > .flex {
                        flex-direction: column;
                        align-items: center;
                        .flex {
                            justify-content: center;
                        }
                    }
                    .sitter_info {
                        width: 100%;
                        text-align: center;
                    }
                    .btn {
                        margin-bottom: 10px;
                    }
                }
            }
        }
        &-head {
            font-family: $fb;
            font-size: 22px;
            margin-bottom: 24px;
        }
    }
}
.card {
    white-space: nowrap;
}
.sitter {
    &_main {
        padding: 24px;
        display: flex;
        &_b {
            padding: 24px 0 0 0;
        }
        @media (max-width:480px) {
            display: block;   
        }
    }
    &_img {
        margin-right: 16px;
        @media (max-width:450px) {
            margin-right: 0;   
        }
    }
    &_info {
        &-name {
            font-size: 28px;
            font-family: $fb;
            i {
                font-style: normal;
                color: $green;
            }
        }
        &-slogan {
            font-size: 15px;
            font-family: $fb;
            line-height: 25px;
        }
        &-adress {
            color: #686868;
            font-size: 15px;
            font-family: $fm;
            margin-bottom: 13px;
        }
        .btn {
            padding: 4px 7px;
        }
        &-btn {
            &-b {
                padding-top: 6px;
                padding-bottom: 30px;
                align-items: center;
                
                .btn {
                    margin-right: 10px;
                }
            }
        }
        &-cost {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 25px;
            .block_head {
                padding-bottom: 0;
            }
            &-block {
                color: #767676;
                font-size: 14px;
                text-align: center;
                white-space: nowrap;
                b {
                    display: block;
                    font-family: $fb;
                    font-size: 32px;
                    line-height: 36px;
                    color: #000;
                }
            }
        }
        &-mess {
            .btn {
                &.green {
                    width: 100%;
                    margin-right: 10px;
                }
                &:not(.green) {
                    padding: 12px 14px;
                    color: $green;
                    font-size: 19px; 
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    &:hover {
                        color: $white;
                    }
                }
            }
            
        }
        &-bottom {
            color: #7c7c7c;
            font-size: 16px;
            justify-content: space-between;
            padding: 30px 0 0 0;
            span {
                color: $black;
                display: inline-block;
            }
        }
    }
    
}
