.client {
    &_main {
        & > div {
            margin-bottom: 30px;
        }
        &-block { 
            box-shadow: 0 1px 25px #d2f1e0;
            border-radius: 10px;
//            padding: 30px;
            padding: 15px;
            height: 100%;
            font-family: $fm;
            &_p {
                height: auto;
                margin-bottom: 20px;
                padding-bottom: 40px;
                padding-top: 30px;
            }
        }
        &-img {
            margin-right: 15px;
            min-width: 150px;
            min-height: 150px;
            height: 150px;
            width: 150px;
            overflow: hidden;
            img {
                width: 100%;
            }
        }
        .btn_client {
            padding: 7px 10px;
            font-size: 13px;
        }
        .btn {
            font-family: $fb;
        }
        .block_head {
            font-family: $fb;
        }
        &-balance {
            padding-top: 10px;
            & > .flex {
                flex-wrap: wrap;
                justify-content: space-between;
                & + a {
                    margin-top: 15px;
                    width: 100%;
                }
            }
        }
    }
}