footer {
    background: url(../images/temp/3.png) no-repeat top center;
    background-size: cover;
    padding-top: 47px;
    padding-bottom: 50px;
    
    color: $textgreen;
    font-size: 17px;
    line-height: 23px;
    a {
        font-size: 15px;
        line-height: 23px;
        color: $textgreen;
        display: block;
        text-decoration: none;
        &:hover {
            text-decoration: underline;
        }
    }
    
}
.footer {
    &_head {
        color: $white;
        font-size: 17px;
        padding-bottom: 10px;
    }
    &_social {
        display: flex;
        padding-top: 20px;
        a {
            font-size: 23px;
            text-decoration: none;
            transition: ease all 0.4s;
            margin-right: 15px;
            &:hover {
                color: #fff;
                text-decoration: none;
            }
        }
    }
    &_left {
        & > div {
            margin-bottom: 15px;
        }
    }
}
.footer {
    &_top {
        background: $lgreen;
        padding-bottom: 75px;
        padding-top: 60px;
        @media (max-width:500px) {
            padding-bottom: 20px;
            padding-top: 20px;
        }
        a {
            display: block;
            font-size: 18px;
            line-height: 23px;
            color: $textgreen;
            text-decoration: none;
            &:hover {
                text-decoration: underline;
            }
        }
    }
}
form.green_form {
        display: flex;
        padding: 10px 0;
        align-items: stretch;
        font-size: 18px;
        input {
            background: #204531;
            color: $textgreen;
            border-radius: 5px;
            margin-right: -10px;
            padding-right: 15px;
            font-family: $fm;
        }
        button {
            background: $textgreen;
            color: $white;
            padding: 0 22px;
            display: flex;
            justify-content: center;
            align-items: center;
            white-space: nowrap;
            border-radius: 5px;
            font-family: $fm; 
            font-size: 18px;
        }
    &_light {
        input {
            background: $lgreen;
        }   
    }
}
.admin { 
    & > .container-fluid {
        height: 100%;
        min-height: 100%;
        & > .row {
            height: 100%;
            min-height: 100%;
        } 
    }
    &-left {
        background: #191f1d; 
    }
    .logo {
        padding-top: 15px;
        padding-bottom: 30px;
        display: block;
        
        img {
            width: 100%;
            
        }
    }
    &-menu {
        width: calc( 100% + 30px );
        margin-left: -15px;
        a {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 24px 15px;
            
            &.active,
            &:hover {
                background: #282e2a;
                color: $green;
                i + i {
                    color: $green;;
                }
            }
            & + a {
                border-top: 1px solid #282e2a;
            }
            i {
                color: $green;
                margin-right: 7px;
                font-size: 20px;
                font-style: normal;
                &:first-child {
                    min-width: 29px;
                }
                & + i {
                    color: #fff;
                }
            }
            b {
                display: block;
                padding: 4px 9px;
                border-radius: 4px;
                background: $green;
                color: #fff;
                font-weight: normal;
            }
            span {
                display: flex;
                align-items: center;
            }
        } 
    }
    .section_head {
        padding-bottom: 0;
        margin-bottom: 0;
    }
    &-right {
        padding-top: 15px;
        padding-bottom: 15px;
        .input_data {
            max-width: 170px;
        }
    }
    &_sitter_stat {
        font-size: 16px;
            padding-bottom: 20px;
        
        .flex {
            min-height: 90px;
            justify-content: flex-start;
            align-items: center;
            margin-bottom: 5px;
            border-radius: 5px;
            &.center {
                justify-content: center;
            }
        }
        b {
            font-size: 34px;
            padding: 0 7px;
            white-space: nowrap;
        }
    }
    .client_main-block {
        height: calc( 100% - 20px );
    }
}

.footer_last {
    @media (max-width:500px) {
        margin-bottom: 15px;
    }
}
.sitter_info-btn {
    .btn {
        margin-bottom: 3px;
    }
}
